import "./App.css";
import React from "react";
import { initializeApp } from "firebase/app";
import {
  getFirestore,
  collection,
  query,
  orderBy,
  limit,
  doc,
  setDoc,
  addDoc,
  limitToLast,
} from "firebase/firestore";
import {
  getAuth,
  GoogleAuthProvider,
  signInWithPopup,
  signOut,
} from "firebase/auth";

import { useAuthState } from "react-firebase-hooks/auth";
import { useCollectionData } from "react-firebase-hooks/firestore";
import Filter from "bad-words";

const app = initializeApp({
  apiKey: "AIzaSyCAytks3dANDKYb_CKznzHkQhkOyRtKBQY",
  authDomain: "chat-app-24162.firebaseapp.com",
  projectId: "chat-app-24162",
  storageBucket: "chat-app-24162.appspot.com",
  messagingSenderId: "847337751263",
  appId: "1:847337751263:web:3a81589e851b05efd31f6a",
  measurementId: "G-7EJL2S9G54",
});

const auth = getAuth(app);
const db = getFirestore(app);

function App() {
  const [user] = useAuthState(auth);

  return (
    <div className="App">
      <header>
        <h1>Chat App</h1>
        {user ? <SignOut /> : <SignIn />}
      </header>
      {
        /* if user show chatroom else show nothing */
        user ? <ChatRoom /> : <></>
      }
    </div>
  );
}

function SignIn() {
  // Sign in with Google
  const signInWithGoogle = () => {
    const provider = new GoogleAuthProvider();
    signInWithPopup(auth, provider);
  };
  return <button onClick={signInWithGoogle}>Sign in with Google</button>;
}

function SignOut() {
  // Sign out
  return (
    auth.currentUser && <button onClick={() => signOut(auth)}>Sign Out</button>
  );
}

function ChatRoom() {
  // Reference to messages collection
  const messagesRef = collection(db, "messages");
  const bannedRef = collection(db, "banned");
  // Query documents in collection
  // take last 25 messages ordered by createdAt
  const q = query(messagesRef, orderBy("createdAt"), limitToLast(25));
  // Listen to data with a hook
  const [messages] = useCollectionData(q, { idField: "id" });

  let [formValue, setFormValue] = React.useState("");
  const dummy = React.useRef();

  const sendMessage = async (e) => {
    e.preventDefault();
    // censor bad words
    const filter = new Filter();
    if (filter.isProfane(formValue)) {
      const cleaned = filter.clean(formValue);
      formValue = ` I got banned for life for saying... ${cleaned}`;
      console.log(formValue);
    }
    setFormValue("");
    await addDoc(messagesRef, {
      text: formValue,
      createdAt: new Date(),
      uid: auth.currentUser.uid,
      photoURL: auth.currentUser.photoURL,
    });
    await addDoc(bannedRef, {
      uid: auth.currentUser.uid,
    });
    dummy.current.scrollIntoView({ behavior: "smooth" });
  };
  return (
    <>
      <div style={{ marginBottom: "60px" }}>
        {messages &&
          messages.map((msg) => <ChatMessage key={msg.id} message={msg} />)}
        <div ref={dummy}></div>
      </div>
      <form
        onSubmit={sendMessage}
        style={{
          position: "fixed",
          bottom: "0",
          marginTop: "20px",
          width: "100%",
        }}
      >
        <input
          value={formValue}
          onChange={(e) => setFormValue(e.target.value)}
        />
        <button type="submit">Send</button>
      </form>
    </>
  );
}

function ChatMessage(props) {
  const { text, uid, photoURL } = props.message;
  const messageClass = uid === auth.currentUser.uid ? "sent" : "received";

  return (
    <div className={`message ${messageClass}`}>
      <img src={photoURL} alt="user" />
      <p>{text}</p>
    </div>
  );
}

export default App;
